import React, { useState } from 'react';

import { Button, Modal, ModalBody, ModalHeader } from 'reactstrap';
import dateFormat, { masks } from 'dateformat';
import deptName from './DepartmentName';

export default function ModalStaffInfo({ staff, id }) {
  if (id === undefined) {
    id = '';
  }
  const [isOpen, setIsOpen] = useState(false);
  return (
    <React.Fragment>
      <Button
        id={'button' + id}
        type='button'
        onClick={() => setIsOpen(!isOpen)}
        className='btn-success mx-1 flex-fill'
      >
        <span className='fa fa-info text-white'></span>
      </Button>
      <Modal
        flip
        isOpen={isOpen}
        target={'button' + id}
        toggle={() => setIsOpen(!isOpen)}
        placement='bottom'
        className='text-dark'
      >
        <ModalHeader>ID: {id} </ModalHeader>
        <ModalBody>
          Ngy sinh: {dateFormat(staff.doB, 'dd/mm/yyyy')} <hr />
          Ngy vo: {dateFormat(staff.startDate, 'dd/mm/yyyy')} <hr />
          Phng ban: {deptName(staff.departmentId)} <hr />
          S ngy ngh cn li: {staff.annualLeave} <hr />
          S gi Lm thm: {staff.overTime} <hr />
          Lng nhn: {staff.salary}
        </ModalBody>
      </Modal>
    </React.Fragment>
  );
};