import React, {useState} from "react";
import { Button, Label, Modal, ModalBody, ModalHeader, Row, Col } from "reactstrap";
import { Control, LocalForm, Errors } from "react-redux-form";

export default function AddStaffModal({ postStaff }){
    const required = (val) => val && val.length;
    const maxLength = (len) => (val) => !val || val.length <= len;
    const minLength = (len) => (val) => val && val.length >= len;
    const isNumber = (val) => !isNaN(Number(val));
    const minValue = (min) => (val) => val && val >= min;
    const maxValue = (max) => (val) => !val || val <= max;
    const [isModalOpen, setIsModalOpen] = useState(false);

    const submitHandler = (values) => {
        console.log(values);
        postStaff(
            values.name,
            values.doB,
            parseFloat(values.salaryScale),
            values.startDate,
            values.department,
            values.annualLeave,
            parseFloat(values.overTime)
        );

        setIsModalOpen(!isModalOpen);
    };

    return(
        <>
            <div className="fw-bold ms-auto col-lg-2 col-md-12 col-sm-12 col-12 p-2">
                <Button
                    className="w-100 p-2 text-white fw-bold"
                    color="success"
                    onClick={() => setIsModalOpen(!isModalOpen)}
                    title='Thm nhn vin'
                >
                    <span className="fa fa-user-plus fa-lg text-white"></span> Thm nhn vin
                </Button>
            </div>

            <Modal isOpen={isModalOpen} size='lg' toggle={() => setIsModalOpen(!isModalOpen)}>
                <ModalHeader toggle={() => setIsModalOpen(!isModalOpen)}>
                    Thm nhn vin
                </ModalHeader>
                <ModalBody>
                    <LocalForm onSubmit={(values) => submitHandler(values)}>
                        <Row className='form-group mb-2'>
                            <Label htmlFor='name' md={4}>
                                Tn
                            </Label>
                            <Col md={8}>
                                <Control.text 
                                    model='.name'
                                    required
                                    type='text'
                                    id='name'
                                    name='name'
                                    placeholder='Nhp tn y '
                                    className='form-control border border-success'
                                    validators={{
                                      required,
                                      minLength: minLength(6),
                                      maxLength: maxLength(30),
                                    }}
                                />
                                <Errors 
                                    className='text-danger'
                                    model='.name'
                                    show='touched'
                                    messages={{
                                      required: 'Tnn khng c  trng, ',
                                      minLength: 'Phi di trn 6 k t',
                                      maxLength: 'Phi t hn 30 k t',
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className='form-group mb-2'>
                            <Label htmlFor='doB' md={4}>
                                Ngy sinh
                            </Label>
                            <Col md={8}>
                                <Control.input
                                    model='.doB'
                                    id='doB'
                                    name='doB'
                                    type='date'
                                    className='form-control border border-success'
                                    validators={{
                                        required,
                                    }}
                                />
                                <Errors
                                    className='text-danger'
                                    model='.doB'
                                    show='touched'
                                    messages={{
                                        required: 'Khng c trng',
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className='form-group mb-2'>
                            <Label htmlFor='startDate' md={4}>
                                Ngy vo cng ty
                            </Label>
                            <Col md={8}>
                                <Control
                                    model='.startDate'
                                    type='date'
                                    id='startDate'
                                    name='startDate'
                                    className='form-control border border-success'
                                    validators={{
                                        required,
                                    }}
                                />
                                <Errors
                                    className='text-danger'
                                    model='.startDate'
                                    show='touched'
                                    messages={{
                                        required: 'Khng c trng',
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className='form-group mb-2'>
                            <Label htmlFor='deparment' md={4}>
                                Phng ban
                            </Label>
                            <Col md={{ size: 8, offset: 0 }}>
                                <Control.select
                                    model='.department'
                                    id='department'
                                    name='department'
                                    className='form-control border border-success'>
                                    <option value='' disabled selected>
                                        Chn phng ban
                                    </option>
                                    <option value='Dept04'>CNTT</option>
                                    <option value='Dept02'>Nhn s</option>
                                    <option value='Dept03'>Ti chnh</option>
                                    <option value='Dept01'>Kinh doanh</option>
                                    <option value='Dept05'>Marketing</option>
                                </Control.select>
                            </Col>
                        </Row>
                        <Row className='form-group mb-2'>
                            <Label htmlFor='salaryScale' md={4}>
                                H s lng
                            </Label>
                            <Col md={8}>
                                <Control
                                    model='.salaryScale'
                                    type='number'
                                    placeholder='1.0 -> 3.0'
                                    id='salaryScale'
                                    name='salaryScale'
                                    rows='12'
                                    className='form-control border border-success'
                                    validators={{
                                        required,
                                        minValue: minValue(1),
                                        maxValue: maxValue(3),
                                        isNumber,
                                    }}
                                />
                                <Errors
                                    className='text-danger'
                                    model='.salaryScale'
                                    show='touched'
                                    messages={{
                                        required: 'Khng c  trng, ',
                                        minValue: 'Khng c nh hn 1',
                                        maxValue: 'Khng c ln hn 3',
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className='form-group mb-2'>
                            <Label htmlFor='annualLeave' md={4}>
                                S ngy ngh cn li
                            </Label>
                            <Col md={8}>
                                <Control
                                    model='.annualLeave'
                                    type='number'
                                    id='annualLeave'
                                    name='annualLeave'
                                    rows='12'
                                    className='form-control border border-success'
                                    validators={{
                                        required,
                                        isNumber,
                                    }}
                                />
                                <Errors
                                    className='text-danger'
                                    model='.annualLeave'
                                    show='touched'
                                    messages={{
                                        required: 'Khng c  trng ',
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className='form-group mb-2'>
                            <Label htmlFor='overTime' md={4}>
                                S ngy i lm thm
                            </Label>
                            <Col md={8}>
                                <Control
                                    model='.overTime'
                                    type='number'
                                    id='overTime'
                                    name='overTime'
                                    rows='12'
                                    className='form-control border border-success'
                                    validators={{
                                        required,
                                        isNumber,
                                    }}
                                />
                                <Errors
                                    className='text-danger'
                                    model='.annualLeave'
                                    show='touched'
                                    messages={{
                                        required: 'Khng c  trng ',
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className='form-group mb-2'>
                            <Col md={{ size: 4, offset: 8 }}>
                                <Button type='submit' color='success'>
                                    Thm nhn vin
                                </Button>
                            </Col>
                        </Row>
                    </LocalForm>
                </ModalBody>
            </Modal>
        </>
    )
};