const deptName = (id) => {
    switch (id) {
      case 'Dept04':
        return ' Phng CNTT';
        break;
      case 'Dept02':
        return ' Phng Nhn s';
        break;
      case 'Dept05':
        return ' Phng Ti chnh';
        break;
      case 'Dept01':
        return ' Phng Kinh Doanh';
        break;
      case 'Dept03':
        return ' Phng Marketing';
        break;
      default:
    }
};

export default deptName;